export const ContentType = {
    Film: 'Film',
    Documentary: 'Documentary',
    Serial: 'Serial',
    Show: 'Show',
} as const;

const ContentTypeRu = {
    Film: '',
    Documentary: '',
    Serial: '',
    Show: '',
}

export function mapContentTypeToRuStr(contentType: keyof typeof ContentType): string {
    return ContentTypeRu[contentType];
}